.counter {
  background-color: rgba(48, 48, 48, 0.85);
  border-radius: 0.25rem 0.25rem 0 0;
  bottom: 0;
  font-size: 0.85rem;
  left: 50%;
  margin: 0 auto;
  padding: 0.25rem 0.5rem;
  position: fixed;
  right: 50%;
  width: fit-content;
  z-index: 2;

  @media print {
    display: none;
  }
}
